@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

body {
  @apply bg-zinc-900 text-zinc-50;
}

.menu-btn {
  @apply h-10 w-10 rounded-xl grid place-items-center bg-zinc-50/10 ring-inset ring-1
   ring-zinc-50/[0.02] backdrop-blur-2xl hover:bg-zinc-50/15 
   transition-[transform,background-color] active:scale-95;
  cursor: pointer;
}
.headline-1 {
  @apply max-w-max;
  background: -webkit-linear-gradient(0deg, #fafafa, #a1a1aa);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.title-1 {
  @apply text-[22px] font-normal;
}

.headline-1 {
  @apply text-5xl leading-tight font-semibold lg:text-[55px] lg:leading-[1.15];
}
.headline-2 {
  @apply text-3xl lg:text-4xl  leading-tight font-semibold;
}
.container {
  @apply px-4 mx-auto lg:px-8 xl:max-w-6xl;
}
.img-box {
  @apply bg-zinc-700 overflow-hidden;
}

.img-cover {
  @apply w-full h-full object-cover;
}
/* navbar */

.navbar {
  @apply absolute rounded-2xl top-full mt-2 right-0 min-w-40 p-2 bg-zinc-50/10  
  ring-inset ring-1 ring-zinc-50/5 scale-90 isolate blur-sm opacity-0 invisible 
  transition-[transform,opacity,filter] md:static md:flex md:items-center md:mt-0 
  md:opacity-100 md:blur-0
  md:visible md:scale-100 backdrop-blur-2xl;
  cursor: pointer;
}

.navbar.active {
  @apply opacity-100 scale-100 blur-0 visible;
}

.nav-link {
  @apply grid items-center h-9 px-4 text-sm font-medium tracking-wide
   text-zinc-50/50 hover:text-zinc-50 transition-colors;
}

.nav-link.active {
  @apply text-zinc-900 duration-500;
}

.navbar .active-box {
  @apply absolute  bg-zinc-50 rounded-lg -z-10 transition-[top,left] duration-500;
}

.btn {
  @apply max-w-max h-9 flex items-center gap-2 px-4 rounded-xl font-medium text-sm ring-1 ring-zinc-50/5
  ring-inset transition-[background-color];
}

.btn-secondary {
  @apply bg-zinc-50 text-zinc-900 active:bg-zinc-50/80;
}

.btn .material-symbols-rounded {
  @apply text-[18px];
}
.btn:has(.material-symbols-rounded) {
  @apply pr-3;
}

.btn-primary {
  @apply h-11 bg-sky-400 text-zinc-950 hover:bg-sky-300 active:bg-sky-400;
}

.btn-outline {
  @apply h-11 bg-zinc-800 hover:bg-zinc-700 active:bg-zinc-800;
}

.section {
  @apply pt-20 lg:pt-32;
}

/* Text Box */

.label {
  @apply block text-zinc-200 text-sm font-normal mb-2;
}

.text-field {
  @apply block outline-none w-full px-3 py-3 text-sm bg-zinc-800 rounded-xl ring-1 ring-inset 
  ring-transparent transition-shadow hover:ring-zinc-50/5 focus:ring-sky-400 focus:ring-2;
}

.text-field::placeholder {
  @apply text-zinc-400;
}

.reveal-up {
  @apply translate-y-20;
}

.gallery-items {
  display: flex;
  gap: 16px;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.5) transparent;
  padding-bottom: 10px;
  width: 100%;
  white-space: nowrap; /* Prevents wrapping */
}

/* Custom scrollbar */
.gallery-items::-webkit-scrollbar {
  height: 8px;
}

.gallery-items::-webkit-scrollbar-track {
  background: #2d2d2d;
}

.gallery-items::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 4px;
}
